@font-face{font-family:"Kimberley";src:url(./font.TTF) format("truetype")}@font-face{font-family:"PingFangBold";src:url(./PingFangBold.ttf) format("truetype")}body{font:14px/1.5 "PingFangBold, 微软雅黑, arial";background:#fff}body,h1,h2,h3,h4,h5,h6,ul,li,dl,dd,p{list-style:none;margin:0;padding:0;border:0}body,a,div,i,img,ul,li{cursor:url("../imgs/mouse.cur"),default}a{color:#cf4646;text-decoration:none}canvas{display:block;vertical-align:bottom}#particles-js{position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,0.8);background-image:url("../imgs/post.jpg");background-repeat:no-repeat;background-size:cover;background-position:50% 50%;overflow-y:scroll}.count-particles{background:#000022;position:absolute;top:48px;left:0;width:80px;color:#13E8E9;font-size:.8em;text-align:left;text-indent:4px;line-height:14px;padding-bottom:2px;font-family:Helvetica, Arial, sans-serif;font-weight:bold}.js-count-particles{font-size:1.1em}#stats,.count-particles{-webkit-user-select:none}#stats{border-radius:3px 3px 0 0;overflow:hidden}.count-particles{border-radius:0 0 3px 3px}#content{position:fixed;width:100%;height:100%;left:0;right:0;margin:auto;padding-top:50px;box-sizing:border-box;overflow-y:scroll}::-webkit-scrollbar{width:14px;height:14px}::-webkit-scrollbar-thumb,::-webkit-scrollbar-track{border-radius:999px;border:5px solid transparent}::-webkit-scrollbar-track{box-shadow:inset 1px 1px 5px rgba(0,0,0,0.8)}::-webkit-scrollbar-thumb{min-height:20px;background-clip:content-box;box-shadow:inset 0 0 0 5px rgba(255,255,255,0.6)}::-webkit-scrollbar-corner{background:transparent}.wrap{position:absolute;left:0;right:0;margin:0 auto;margin-top:50px;width:500px;font-size:22px;padding:40px 20px}.shadow{border:1px solid #e6e6e6;border-radius:5px;background:rgba(255,255,255,0.6)}.list{padding:0 60px;color:#cf4646}.list li{list-style:decimal;line-height:50px;border-bottom:1px solid #f2f2f2}.list li h1{font-size:22px;font-style:normal;font-family:"Hiragino Sans GB","Hiragino Sans GB W3",'微软雅黑'}.list li a{display:block;padding:5px 8px;transition:all .6s}.list li a:hover{text-indent:25px;border-radius:5px;color:#fff;text-shadow:0 2px 1px rgba(0,0,0,0.8);background-color:#DD3A0A;background-image:linear-gradient(to bottom, #DD3A0A, #DD560A);border-color:rgba(0,0,0,0.1) rgba(0,0,0,0.1) rgba(0,0,0,0.25)}.sub-dl{max-height:0;line-height:45px;overflow:hidden;transition:max-height linear .5s}.sub-dl.active{max-height:990px}#pages_0101{width:500px;margin:0 auto;padding-top:100px}#pages_0102{width:600px;margin:0 auto;padding-top:50px}#pages_0404{width:100%;height:970px;background:#282828;padding-top:5%}#pages_0404 .div1200{width:1200px;height:100%;position:relative;margin:0 auto}#pages_0404 .div1200 span.title{display:block;width:184px;border-bottom:4px #3c3c3c solid;cursor:pointer;margin:0 auto}#pages_0404 .div1200 span.title img{width:100%}#pages_0404 .div1200 ul.list{margin-top:30px;overflow:hidden;padding:0}#pages_0404 .div1200 ul.list li{width:210px;height:210px;float:left;margin:0 0 20px 20px;background:#383838;cursor:pointer;position:relative;list-style:none;border-bottom:0;transition:all 0.3s linear;transform:scale(1) translate(0px, 0px);overflow:hidden}#pages_0404 .div1200 ul.list li img{display:block;margin:0 auto;margin-top:10px;height:190px}#pages_0404 .div1200 ul.list li div{position:absolute;z-index:1;width:190px;height:190px;top:10px;left:10px;background:rgba(209,51,49,0.8);transition:all .3s linear;transform-origin:100% 100%;transform:rotate(90deg);opacity:0}#pages_0404 .div1200 ul.list li div i{display:block;height:44px;margin:0 auto;margin-top:50px;background:url("../imgs/y1.png") center center no-repeat}#pages_0404 .div1200 ul.list li div span{width:100%;display:inline-block;text-align:center;font-size:20px;color:#fff;margin-top:10px}#pages_0404 .div1200 ul.list li div p{font-size:12px;text-align:center;color:#fff;height:20px;line-height:20px}#pages_0404 .div1200 ul.list li:hover{background:#4c4c4c}#pages_0404 .div1200 ul.list li:hover div{opacity:1;transform:rotate(0deg)}#pages_0404 .box_bg1{position:fixed;left:0;top:0;z-index:60;background:rgba(0,0,0,0.8);width:100%;height:100%;transform:scale(0);transition:all .2s linear;opacity:0}#pages_0404 .box_ovo{width:940px;height:500px;position:fixed;left:50%;top:50%;margin-left:-470px;margin-top:-250px;background:#ffffff;z-index:100;transition:all .2s linear;opacity:0;transform:scale(0)}#pages_0404 .current{opacity:1;transform:scale(1)}#pages_0404 .close{width:40px;height:40px;position:absolute;right:-40px;top:0;cursor:pointer;background:#d83e40}#pages_0404 .close i{display:block;width:40px;height:40px;background:url("../imgs/close.png") center center no-repeat;transition:all .2s linear}#pages_0404 .close:hover i{transform:rotate(180deg)}
